.icon {
  box-sizing: content-box;
  display: block;
  // @include squareIcon($mobile_icon-size);
  // padding: 3px;
  @include squareIcon($icon-size);
  padding: 4px;

  // background-size: $mobile_icon-size;
  background-size: $icon-size;
  background-position: center;
  background-repeat: no-repeat;

  // @include onTablet {
  //   @include squareIcon($tablet_icon-size);
  //   background-size: $tablet_icon-size;
  //   padding: 4px;
  // }

  @include onDesktop {
    @include hover(scale, 1.1);
  }

  &--phone {
    background-image: url("../images/icons/phone.svg");
  }

  &--menu {
    background-image: url("../images/icons/menu.svg");

    &:hover {
      background-image: url("../images/icons/menu_hover.svg");
    }
  }

  &--close {
    background-image: url("../images/icons/close.svg");
  }
}
